$brand-color: #454545;
$accent-color: yellow;

// Base colors
$base-color: #ededed;

// $brand-color: red;
$brand-80-color: rgba($base-color, 0.8);
$accent-color: yellow;


// -----------------------------------------
//    Typography
// -----------------------------------------

$base-font-size: 16px;
$base-line-height: 1.3;
$scale: 1.5;

// Mixin
@mixin center-block {
   display: block;
   margin-left: auto;
   margin-right: auto;
 }


 @mixin bp($point) {
  @if $point == big-screen {
    @media (max-width: 1600px) { @content; }
  }
  @else if $point == medium-screen {
    @media (max-width: 1250px) { @content; }
  }
  @else if $point == mobile-screen {
    @media (max-width: 600px)  { @content; }
  }
}

 
